import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { ServicedeskComponent } from './servicedesk.component';
import { CreateTicketComponent } from './components/create-ticket/create-ticket.component';
import { MyTicketsComponent } from './components/my-tickets/my-tickets.component';
import { AddTicketComponent } from '../../shared/components/add-ticket/add-ticket.component';
import { HelpdeskAllTicketsComponent } from '../../shared/components/helpdesk-all-tickets/helpdesk-all-tickets.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'create-ticket', pathMatch: 'full'},
	{ path: 'create-ticket', component: AddTicketComponent},
	{ path: 'edit-ticket', component: AddTicketComponent },
	{ path: 'edit-ticket/:id', component: CreateTicketComponent },
	{ path: 'view-ticket', component: HelpdeskAllTicketsComponent},
	{ path: '**', redirectTo: 'create-ticket', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ServicedeskRoutingModule { }
